import Vue from 'vue'
import Vuex from 'vuex'
 Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        showedProduct: 1,
        showedMenu: 0,
        screenWidth: window.screen.width
    },
    mutations: {
        toggleMenu(state) {
            state.showedMenu = ++state.showedMenu % 2;
        },
        hideNavBar(state){
            state.showedMenu = 0;
            state.showedProduct = 1;
        },
        showProduct(state, payload){
            state.showedProduct = payload.idProduct;
        },
        setScreenWidth(state){
            state.screenWidth = window.screen.width;
        }
    }

})

export default store