<template lang="pug">
    //:style="'background-image: url('+this.image+');'"
    .homeImportantProduct.relative.w-full.h-full(:style="'background-image: url('+this.image+');'" @mouseover="showBtn = true" @mouseleave="showBtn = false")
        h3.float-left.ml-3.mt-3(:class="{'underline' : showBtn === true}") {{this.titulo}}
        btn-hover.absolute.right-0.bottom-0.mr-4.mb-4(v-show="showBtn === true" texto1="ver más" main-color="blue-400" hover-color="white" px="10" py="1" font-size="sm" border-size="2" hover-font-color="black")
</template>

<script>
    import BtnHover from "../celulas/BtnHover";
    export default {
        components: {BtnHover},
        props:{
            image: String,
            titulo: String
        },
        name: "HoverCardUnderline",
        data(){
            return {
                showBtn: false
            }
        }
    }
</script>

